import React, { Component } from "react";
import {
  Grid,
  Chip,
  Paper,
  Typography,
  Button,
  Dialog,
} from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import ImagenCircular from "../../Shared/ImagenCircular";
import FrmDialogoSolicitarTutor from "../FrmDialogoSolicitarTutor.js";
import { POST } from "../../../Conexion/Controller";

const styles = {
  paper: {
    borderTop: "3px solid #3AAFA9",
    backgroundColor: "#ffffff",
  },
  chip: {
    textAlign: "center",
  },
  paperDiseibol:{
    borderTop: "3px solid #3AAFA9",
    backgroundColor: "#cccccc",
  }
};
class DisponibilidadCard extends Component {
  //static contextType = UserContext;
  constructor() {
    super();
    this.state = {
      tutores: {
        columns: [
          {
            title: "Nombre",
            field: "nombre",
          },
        ],
        data: [{ nombre: "" }],
      }, //aqui va el nombre de la tablilla
      open: false,
      mensajeDispo: "",
      visible: false,
      actuTys: false,
      fotos: [
        "data:image/jpeg;base64,",
        "data:image/jpeg;base64,",
        "data:image/jpeg;base64,",
        "data:image/jpeg;base64,",
      ],
    };
    this.handleOnClick = this.handleOnClick.bind(this);
    this.handleOnClose = this.handleOnClose.bind(this);
    this.obtenerMensajeRango = this.obtenerMensajeRango.bind(this);

    this.handleOnChangeUpdate = this.handleOnChangeUpdate.bind(this);
  }
  //=============================================================
  async handleOnClick() {
    const yeri = await this.obtenerMensajeRango();
    if (yeri) {
      this.setState({ open: true });
    }
  }

  handleOnClose() {
    ////console.log("ctm",this.state.openSolicitarTutor);
    this.props.refreshCalendarioCitas();
    this.setState({ open: false });
  }

  handleOnChangeUpdate(e) {
    if (e.actualizaMensajillo) {
      this.setState({ actuTys: !this.state.actuTys });
    }
  }

  async componentDidUpdate(prevProps, prevState) {
    if (prevState.actuTys != this.state.actuTys) {
      const nuevaSolicitud = {
        disponibilidad: {
          HORA_INICIO: this.props.disponibilidad.HORA_INICIO,
          HORA_FIN: this.props.disponibilidad.HORA_FIN,
          FECHA: this.props.disponibilidad.FECHA,
          ID_TUTOR: this.props.disponibilidad.ID_TUTOR,
        },
      };
      const props = {
        servicio: "/api/disponibilidadIntervalos",
        request: nuevaSolicitud,
      };
      let sesionTyS = await POST(props);
      if (!sesionTyS) return;

      if (sesionTyS.message) {
        if (sesionTyS.error) {
          //console.log("<mensajeE:", sesionTyS.message);  //error
          return;
        } else {
          if (sesionTyS.message.includes("oKupada")) {
            //console.log("<mensajeNou:", sesionTyS.message);  //sin dispo
            //this.setState({ visible: false });
            return;
          } else {
            //console.log("<mensajeWii:", sesionTyS.message);  //satisfactoriamente
            this.setState({ mensajeDispo: sesionTyS.message }); //<<== la magia
            //this.setState({ visible: true });
            return;
          }
        }
      }
      //return false;
    }
  }

  async obtenerMensajeRango() {
    const nuevaSolicitud = {
      disponibilidad: {
        HORA_INICIO: this.props.disponibilidad.HORA_INICIO,
        HORA_FIN: this.props.disponibilidad.HORA_FIN,
        FECHA: this.props.disponibilidad.FECHA,
        ID_TUTOR: this.props.disponibilidad.ID_TUTOR,
      },
    };
    //console.log("BTN_SOLICITAR WWW", nuevaSolicitud);
    //se llama al back
    const props = {
      servicio: "/api/disponibilidadIntervalos",
      request: nuevaSolicitud,
    };
    let sesionTyS = await POST(props);
    if (!sesionTyS) return;

    if (sesionTyS.message) {
      if (sesionTyS.error) {
        //console.log("<mensajeE:", sesionTyS.message);  //error
        return;
      } else {
        if (sesionTyS.message.includes("oKupada")) {
          //console.log("<mensajeNou:", sesionTyS.message);  //sin dispo
          //this.setState({ visible: false });
          return;
        } else {
          //console.log("<mensajeWii:", sesionTyS.message);  //satisfactoriamente
          this.setState({ mensajeDispo: sesionTyS.message }); //<<== la magia
          //this.setState({ visible: true });
          return true;
        }
      }
    }

    //return false;
  }

  async componentDidMount() {
    // let ojalaPes = this.obtenerMensajeRango();
    // if (ojalaPes) { this.setState({ visible: true }); }
    // else { this.setState({ visible: false }); }
    console.log(">>Dispo", this.props.disponibilidad);
  }

  render() {
    const disponibilidad = this.props.disponibilidad;

    //console.log("disponibilidad: ", disponibilidad);
    return (
      <>
        <Button
          disabled={this.props.diseibol}
          onClick={this.handleOnClick}
          //disabled={!this.state.visible}
        >

          <Paper style={this.props.diseibol?styles.paperDiseibol: styles.paper}>
            {/** Encabezado Imagen y nombre */}
            <Grid container spacing={0}>
              <Grid container spacing={0}>
                <Grid item md={3} xs={3}>
                  {/* <ImagenCircular src="https://www.w3schools.com/howto/img_avatar.png" /> */}
                  {disponibilidad.TUTOR.USUARIO.IMAGEN ? (
                    <ImagenCircular
                      src={`data:image/jpeg;base64,${disponibilidad.TUTOR.USUARIO.IMAGEN}`}
                    />
                  ) : (
                    <ImagenCircular src="https://www.w3schools.com/howto/img_avatar.png" />
                  )}
                </Grid>
                
                <Grid item md={9} xs={9}>
                  <Grid item md={12} xs={12}>
                    <Typography component="paragraph">
                      {disponibilidad.TUTOR.USUARIO?.NOMBRE + " "}
                    </Typography>
                  </Grid>
                  <Grid item md={12} xs={12}>
                    <Typography component="paragraph">
                      {disponibilidad.TUTOR.USUARIO?.APELLIDOS}
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
              {/** horas */}
              <Grid container spacing={0}>
                <Grid item md={1} xs={1} />
                <Grid item md={1} xs={1}>
                  <Typography
                    variant="button"
                    component="h4"
                    style={styles.chip}
                    display="block"
                    gutterBottom
                  >
                    <strong style={styles.chip}>De:</strong>
                  </Typography>
                </Grid>
                <Grid item md={4} xs={4}>
                  <Chip
                    label={disponibilidad?.HORA_INICIO}
                    color="primary"
                    size="small"
                    variant="outlined"
                  />
                </Grid>

                <Grid item md={1} xs={1}>
                  <Typography
                    variant="button"
                    component="h4"
                    style={styles.chip}
                    display="block"
                    gutterBottom
                  >
                    <strong>A:</strong>
                  </Typography>
                </Grid>
                <Grid item md={4} xs={4}>
                  <Chip
                    label={disponibilidad?.HORA_FIN}
                    color="primary"
                    size="small"
                    variant="outlined"
                  />
                  <Grid item md={1} xs={1} />
                </Grid>
              </Grid>
            </Grid>
          </Paper>
        </Button>

        <Dialog
          open={this.state.open}
          onClose={this.handleOnClose}
          aria-labelledby="max-width-dialog-title"
          maxWidth={"md"}
        >
          <DialogContent>
            <FrmDialogoSolicitarTutor
              dispo={this.props.disponibilidad}
              fexaForm={this.props.fexaForm}
              onCloseFrm={this.handleOnClose}
              idPro={this.props.idPro}
              duracionPro={this.props.duracionPro}
              mensajeDispo={this.state.mensajeDispo}
              onChange={this.handleOnChangeUpdate}
            />
          </DialogContent>

          <DialogActions> </DialogActions>
        </Dialog>

        <br />
      </>
    );
  }
}

export default DisponibilidadCard;
